
          @import "@/styles/global.sass"
        


































































































































main
  display: flex
  flex-direction: column
  justify-content: flex-start
  height: 100%

h2
  margin: $grid-gap 0 $grid-gap $grid-gap

.select-text
  text-align: right
  color: $grey
  margin: 1em 2 * $grid-gap $grid-gap 0

.list
  padding: $grid-gap
  display: grid
  grid-gap: $grid-gap
  @include cursor-shadow()
  text-align: center
  margin-bottom: $gap

  label
    position: relative
    cursor: pointer
    opacity: 0.95
    box-shadow: 0px 0px 2px 2px rgba(black, .1)
    display: flex
    flex-direction: column
    align-items: center
    min-height: 40px
    padding: $grid-gap
    background: linear-gradient(to right, $purple-gradient-colors 49.8%, $grid-color 49.8%)
    background-size: 200%
    background-position: 99.8% 0 // 1px glitch
    transition: background .2s ease-in

    h4
      font-weight: normal

  .selected
    transition: background .4s ease-in-out
    background-position: left

.modes
  flex-shrink: 2
  flex-grow: 2
  max-height: 250px
  grid-template-columns: repeat(3, 1fr)

  label
    padding: 30% 0.5em 0.5em

    .modeDesc
      margin: 20% 0
      color: #bbb
      font-size: 0.9em
      width: 80%
      text-align: center


.themes
  grid-template-columns: repeat(2, 1fr)
  grid-template-rows: 50px 50px

  label
    justify-content: space-around

.toggles
  flex-grow: 1
  width: 100%
  position: relative
  padding: 0 3 * $grid-gap 0 2 * $grid-gap
  overflow-y: auto

  &::-webkit-scrollbar
    width: $gap / 2

  &::-webkit-scrollbar-thumb
    background: linear-gradient(to top, $purple-gradient-colors)

  &::-webkit-scrollbar-track
    background-color: $grid-color
  &::-webkit-scrollbar-corner
    background-color: $grid-color


  label
    display: flex
    justify-content: space-between
    width: 100%
    position: relative
    cursor: pointer
    margin-top: $gap

  $width: 70px
  $trans-duration: .15s
  .slider
    position: relative
    width: $width
    height: 20px
    background: linear-gradient(to right, $purple, $light-purple 50%, $grey 50% 100%)
    background-size: 200%
    background-position: right
    @include shadow(0.5)
    transition: background $trans-duration ease-in-out, transform 0.07s ease-in-out

  .slider:after
    content: ''
    z-index: 3
    left: -0.5rem
    top: calc((1.6rem - 20px) / -2)
    width: 1rem
    height: 1.6rem
    position: absolute
    background-color: $white
    @include shadow()
    transition: transform $trans-duration ease-in-out

  input:active + .slider:after
    transform: scale(0.75)

  input:checked + .slider
    background-position: left

  input:checked + .slider:after
    transform: translateX($width)

@media (max-height: 700px)
  .toggles
    label
      margin-top: 1.5em
  .list
    margin-bottom: 1em

