
          @import "@/styles/global.sass"
        

































































































































































































































































































.keyboard
  margin: 0 auto
  display: flex
  flex-direction: column
  justify-content: space-around
  font-size: 1rem
  --min-size: 4vw
  --key-margin: .1rem
  --key-size: calc(var(--min-size) + var(--key-margin) * 2)
  min-width: calc(15 * var(--key-size))
  height: calc(5 * var(--key-size))
  @include cursor-shadow()


.row
  text-align: center
  position: relative
  width: 100%
  display: flex
  height: 0
  margin: var(--key-margin) 0
  flex-grow: 1
  align-items: center


.key
  height: 100%
  cursor: pointer
  user-select: none
  flex-shrink: 0
  flex-basis: 0
  margin: 0 var(--key-margin)
  padding: 0.4rem
  background: $grid-color
  display: flex
  flex-direction: column
  justify-content: space-around
  transition: background-color 0.3s cubic-bezier(0,.5,1,1), color 0.3s cubic-bezier(0,.5,1,1), transform .1s ease-out


span
  pointer-events: none


.key:hover
  opacity: .9


.key:active
  background: white
  color: $grid-color !important
  transform: scale(.96)


.square
  flex-grow: 1

.tab
  flex-grow: 1.4

.caps-lock
  flex-grow: 1.7

.shift.left
  flex-grow: 2.1

.row:last-child > .special:not(.space)
  flex-grow: 1.4

.backspace
  flex-grow: 2

.backslash
  flex-grow: 1.6

.enter
  flex-grow: 2.3

.shift.right
  flex-grow: 2.9

.space
  flex-grow: 5.2


.secondary
  color: #bbb


.left > span, .right > span
  align-self: flex-start


.right > span
  align-self: flex-end


.special > span
  font-size: 0.75rem

[wrong-count]
  background: $accent1
  filter: unquote("saturate(calc(var(--wrong-count) * 20% + 60%))")

[expected-count]
  --color: calc(var(--expected-count) * 40 + 190)
  background: rgba(var(--color),var(--color),var(--color), 0.6)
  color: rgb(43, 30, 51) !important

[wrong-count][expected-count]
   background: rgba(calc(var(--color) + 20), calc(var(--color) - 30), calc(var(--color) + 50), 1)


.locked
  outline: 2px solid var(--accent2)

