
          @import "@/styles/global.sass"
        








































































































































































.tab-settings
  height: 40px
  .tab-size-option
    display: inline-flex
    flex-direction: column
    justify-content: space-around
    align-items: center
    width: 3em
    height: 3em
    margin-left: 1em
    cursor: pointer
    box-shadow: 0px 0px 2px 2px rgba(black, .1)
    background: linear-gradient(to right, $purple-gradient-colors 49.8%, $grid-color 49.8%)
    background-size: 200%
    background-position: 99.8% 0 // 1px glitch
    transition: background .2s ease-in
  .selected
    transition: background .4s ease-in-out
    background-position: left

.warning
  pointer-events: none
  height: 40px
  line-height: 40px
  position: absolute
  width: 100%
  text-align: right

  .arrow
    margin-left: 1em

.codemirror
  flex-grow: 1
  opacity: 0
  // width: 100%
  margin-top: $gap
  transition: opacity .5s ease-in
  position: relative

  ::v-deep .CodeMirror-scroll
    &::-webkit-scrollbar
      display: none

  ::v-deep .CodeMirror-vscrollbar
    &::-webkit-scrollbar
      width: $gap / 2
    &::-webkit-scrollbar-thumb
      background: linear-gradient(to top, $purple-gradient-colors)
    &::-webkit-scrollbar-track
      background-color: $grid-color
    &::-webkit-scrollbar-corner
      background-color: $grid-color

.ready
  opacity: 1

.buttons
  margin-top: 3em

