
          @import "@/styles/global.sass"
        





























































































































































































.start
  position: relative
  display: flex
  justify-content: flex-end
  height: calc(100vh - 2 * #{$gap})
  outline: none

.middle
  position: relative
  flex-basis: 0
  flex-grow: 2
  max-width: 50%
  margin-right: $gap * 2
  display: flex
  flex-direction: column
  justify-content: space-between

.upload-scroll
  flex-grow: 1
  max-height: 85vh
  overflow: hidden
  position: relative

  .code-editor
    height: 100%
    display: flex
    flex-direction: column


.buttons-bottom
  display: flex
  justify-content: space-between
  align-items: flex-end
  margin-top: $gap
  margin-bottom: $grid-gap

.button
  display: flex
  text-align: center
  justify-content: space-around
  align-items: center
  width: 150px
  height: 47px
  background: $grid-color
  margin-right: $gap
  cursor: pointer
  flex-grow: 1
  max-width: 250px

.start-btn, .ready-btn
  background: linear-gradient(to right, $purple, $light-purple 50%, $grid-color 50% 100%)
  background-size: 200%
  background-position: right

  transition: background .2s ease-in

.start-btn
  margin-right: 0

.highlight
  background-position: left
  transition: background 1.2s ease-out

.languages-list
  position: relative
  max-width: 40%
  flex-grow: 4
  flex-basis: 0
  // height: calc(100vh - 2 * #{$gap})
  display: flex
  flex-direction: column

