
          @import "@/styles/global.sass"
        













































































































































































































.stats
  display: flex
  justify-content: space-between
  width: 100%
  max-width: 100%
  position: relative

  .flex-column
    display: flex
    flex-direction: column
    justify-content: flex-start
    min-width: 400px
    width: 450px
    position: relative

    &.left
      flex-shrink: 2

    &.right
      flex-grow: 1
      margin-left: 4vw

.flex-item
  overflow: hidden
  min-width: 100%
  width: 100%
  height: 400px
  min-height: 400px
  max-height: 400px
  margin-bottom: 2em
  position: relative

.lines, .mixed
  width: 100%

.pie
  width: 400px

.big-stats
  width: 100%
  display: flex
  justify-content: space-between
  text-align: center
  margin-bottom: 1rem
  font-size: 3rem

  .unit
    font-size: .5em

.main-stats
  display: flex
  flex-direction: column
  justify-content: space-between

  .wpm-chart
    width: 100%
    height: 100%
    max-height: 150px
    flex-grow: 1

  .middle
    height: 6rem
    display: flex
    justify-content: space-between
    position: relative
    align-items: stretch
    .mistakes-info
      display: flex
      justify-content: space-between
      flex-direction: column
    .share
      display: block
      height: 100%
      width: 6rem
      position: relative
      text-align: center
      background: $grid-color
      transition: transform .5s

      &:focus
        transform-origin: right
        transform: scale(4)
