
          @import "@/styles/global.sass"
        































































































































.search
  display: flex
  justify-content: space-between
  position: relative
  margin: 0 $grid-gap
  padding: $grid-gap
  border-bottom: $grid-gap solid $grid-color
  height: 40px


  &:focus-within
    border-image: linear-gradient(to right, $light-blue, $grid-color 90%) 1

  input
    max-width: 40%

  input::placeholder, .nothing-found-text
    color: $grey

  div
    overflow: hidden

  .clear-btn
    margin-left: 1em
    background: $navy-grey
    padding: $grid-gap 4 * $grid-gap
    box-shadow: 0px 0px 2px 2px rgba(black, .1)
    height: 100%

.list
  flex-grow: 1
  position: relative
  @include cursor-shadow()
  padding: $grid-gap
  display: grid
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr))
  grid-gap: $grid-gap
  text-align: center
  overflow-y: auto

.list
  &::-webkit-scrollbar
    width: $gap / 2
  &::-webkit-scrollbar-thumb
    background: linear-gradient(to bottom, $blue-gradient-colors)

  &::-webkit-scrollbar-track
    background-color: $grid-color

.language-radio
  display: none

.language
  min-height: 40px
  position: relative
  cursor: pointer
  background: $navy-grey
  opacity: 0.95
  box-shadow: 0px 0px 2px 2px rgba(black, .1)
  display: flex
  justify-content: space-around
  align-items: center
  background: linear-gradient(to right, $blue-gradient-colors 49.8%, $grid-color 49.8%)
  background-size: 200%
  background-position: 99.8% 0 // 1px glitch
  transition: background .1s ease-in

  &:hover
    opacity: 0.85

.stat
  right: 0
  width: 3em
  height: 100%
  display: flex
  flex-direction: column
  align-items: center
  justify-content: center
  flex-wrap: wrap
  position: absolute
  padding: 0.5em 0.3em
  opacity: 0
  color: $grey
  font-size: 0.9em



.language:hover > .stat
  opacity: 1

.selected
  background-position: left
  transition: background 0.3s ease-in-out

.selected:hover > .stat
  opacity: 0

