
          @import "@/styles/global.sass"
        


































































*
  box-sizing: border-box
  margin: 0
  padding: 0


body
  font-family: Helvetica, Arial, sans-serif
  margin: 0
  background: radial-gradient(at top left, $navy-grey ,$light-navy)
  background-repeat: no-repeat
  background-attachment: fixed
  overflow: auto
  &::-webkit-scrollbar
      width: $gap / 2
  &::-webkit-scrollbar-thumb
    background: $washed-purple
    background: linear-gradient(to bottom,  $washed-purple, mix($washed-purple, $grid-color, 60%))
  &::-webkit-scrollbar-track
    background-color: $grid-color
  &::-webkit-scrollbar-corner
    background-color: $grid-color




#app
  position: relative
  width: 100%
  min-height: 100vh
  color: $white
  padding: $gap
  display: flex
  justify-content: space-between

aside
  min-width: $nav-size
  margin-right: $gap * 2
  padding: $grid-gap
  flex-shrink: 0
  @include cursor-shadow()
  @include shadow()
  transition: transform $nav-trans-dur $nav-trans-timing 0s, min-width .5s ease-in-out

  &.wide:not(.thin)
    min-width: 15vw

  &.thin
    transition-delay: $nav-trans-dur
    transform: translateX(calc(-100% + #{$nav-move}))
    margin-right: calc( -1 * min(150px, calc(300px - 20vw)))


main
  flex-grow: 1
  position: relative
  min-width: 550px
  min-width: 0

button, a, input[type="checkbox"], input[type="radio"]
  cursor: pointer

.CodeMirror, .CodeMirror-gutters
  font-size: 1.5rem
  height: auto !important
  color: white
  background: transparent !important

  // if no theme loaded
.CodeMirror
  color: white !important

.CodeMirror pre.CodeMirror-line
  color: white !important


.CodeMirror-linenumber
  width: 1rem !important


.cm-error:not(.mark)
  background-color: transparent !important

@media (max-width: 900px), (max-height: 700px)
  .toggles
    label
      margin-top: 1em
  .list
    margin-bottom: 1em

  #app
    padding: 1em

  aside:not(.thin)
    margin-right: 1em

