
          @import "@/styles/global.sass"
        







































































































































































main
  min-height: calc(100vh - #{2 * $gap})
  max-width: 1300px
  width: 100%
  position: relative
  flex-direction: column
  display: flex
  justify-content: flex-start

  &.wide
    max-width: 1600px

  .code-editor
    flex-grow: 1
    margin: 1rem 0

.top-bar
  display: flex
  justify-content: space-between
  align-items: center
  position: relative
  animation: opacity-enter .5s ease-out forwards .7s
  animation-fill-mode: both

  .info
    position: relative
    display: flex
    align-items: center
    min-width: 0

    .languageName
      font-size: 2rem
      margin-right: 1em
    .codeInfo
      display: flex
      justify-content: space-between
      flex-direction: column
      overflow: hidden
      p
        margin-bottom: $grid-gap

  .buttons
    flex-shrink: 0
    button
      text-align: center
      min-width: 150px
      padding: 0 0.5em
      height: 47px
      background: $grid-color
      margin-left: max(10px, calc(20vw - 210px))

.results
  width: 100%
  max-width: 100%
  padding-top: 1rem

