eft:0;right:0;font-size:1.3em;text-align:center;color:#404f7d;transform:translateY(-0.1em) scaleX(1.5)}.completed{pointer-events:auto}.completed .codemirror ::v-deep .CodeMirror-line .next-char{border-bottom:none}.completed .codemirror ::v-deep .CodeMirror-line .corrected{background-color:#31324a;opacity:1;display:inline-block;filter:none;padding:.1em 0}.completed .codemirror ::v-deep .CodeMirror-line .corrected.alone{padding:.1em .2em}.completed .codemirror ::v-deep .CodeMirror-line .corrected.left-most{padding-left:.2em}.completed .codemirror ::v-deep .CodeMirror-line .corrected.right-most{padding-right:.2em}.pop-up{position:fixed;top:0;bottom:0;left:0;right:0;background-color:rgba(41,42,62,.5);display:flex;justify-content:space-around;align-items:center;pointer-events:all;user-select:none;z-index:10;font-size:4rem;opacity:1;animation:opacity-enter .7s ease-out forwards;cursor:default;text-align:center}.pop-up.hidden{pointer-events:none;animation:opacity-leave .4s ease-out forwards .1s}.pop-up.clickable h2{cursor:pointer}.pop-up.small-font{font-size:1.5rem}.pop-up.small-font h2{max-width:50vw;text-align:center}.pop-up p{animation:opacity-enter .5s ease-out forwards .7s;animation-fill-mode:backwards;font-size:2rem;transform:translateY(4rem)}