
          @import "@/styles/global.sass"
        














































































@mixin padding-left
  padding-left: 1.2rem
  padding-right: 1.2rem

.title
  // margin-top: 4% // 10-6
  font-size: 2rem
  font-weight: 600
  padding: 1.1rem 0
  width: 100%
  position: relative
  transition: opacity $nav-trans-dur $nav-trans-timing $nav-trans-dur, background-color .15s ease-in-out
  @include padding-left
  @include navbar-mouse-effect

  .beta
    font-size: 13px
    position: absolute
    padding-left: 3px

nav
  height: 100%
  padding: 6% 0
  display: flex
  flex-direction: column
  justify-content: flex-start
  background: linear-gradient(340deg, $navy-grey, $navy-grey 20%, $washed-purple)
  // border-radius: 3px
  font-size: 1rem
  position: relative

nav:after
  content: ''
  position: absolute
  pointer-events: none
  @include pos0
  transition: backdrop-filter $nav-trans-dur $nav-trans-timing 0s

.thin:after
  // backdrop-filter: hue-rotate(20deg)
  transition-delay: 1s
  backdrop-filter: hue-rotate(10deg) brightness(80%)

.thin
  $translate: translateX(calc(#{$nav-size} - 2.5em - #{$nav-move} / 2))
  .btn-text, .title, .room
    transition-delay: 0s
    opacity: 0
  svg:not(.heart)
    transition-delay: $nav-trans-dur
    transform: $translate
  .flip
    transform: $translate rotate(180deg) !important
  .author
    opacity: 0
    transition-delay: $nav-trans-dur

.btn-text, .room, .author
  transition: opacity $nav-trans-dur $nav-trans-timing $nav-trans-dur

.links
  margin: 6% 0 10% 0
  $line: 1px solid $grey
  border-top: $line
  border-bottom: $line
  min-height: 40%
  display: flex
  flex-direction: column
  justify-content: space-evenly
  transition: min-height .5s ease-in-out


  .link
    display: block
    width: 100%
    padding: 1.1rem
    @include padding-left
    @include navbar-mouse-effect

  .line
    width: 100%
    // border-bottom: 1px solid $grey

  .btn-text
    margin-left: 1em

.links.room-connected
  min-height: 25%

svg
  display: inline-block
  width: 1em !important
  transition: transform $nav-trans-dur $nav-trans-timing 0s

.room
  flex-grow: 1
  @include padding-left

.author
  @include padding-left

  .author-text
    font-size: .9em

    .author-name
      white-space: nowrap

