
          @import "@/styles/global.sass"
        












































































































































































































































































@mixin small-btn
  text-align: center
  width: 40%
  padding: $grid-gap
  border-left: 1px solid $grey
  @include navbar-mouse-effect

svg
    margin-top: $grid-gap

input
  margin-left: 1em
  border-bottom: 1px solid $grey
  overflow: hidden
  padding: $grid-gap

.roomNotConnected
  display: flex
  flex-direction: column
  justify-content: space-between

  .popUp
    position: fixed
    top: 0
    bottom: 0
    left: 0
    right: 0
    background-color: rgba($grid-color, .7)
    display: flex
    justify-content: space-around
    align-items: center
    pointer-events: all
    user-select: none
    z-index: 10
    transition: opacity 2s ease-in-out

    &.hide-popUp
      opacity: 0

    .wrapper
      width: 50vw
      text-align: center
      display: flex
      align-items: center
      flex-wrap: wrap
      flex-direction: column

      h2
        font-size: 2.5rem

      .playerName
        margin: 3rem 0
      .playerName, .buttons
        width: 40%
        max-width: 250px

      button
        width: 100%
        height: 35px

  button
    @include small-btn

  .roomName, .playerName
    display: flex
    justify-content: space-between
    margin: 1em 0

  .buttons
    display: flex
    justify-content: space-between


  .info
    margin-top: 1em

.roomConnected
  .popUp
    margin-bottom: 2em
    .shareLink
      display: flex
      justify-content: space-between
      margin: 1em 0

    input
      flex-grow: 1

    .close-btn
      @include small-btn

  .roomNameContainer
    display: flex
    justify-content: space-between
    align-items: center
    margin-bottom: 1em

    svg
      margin-bottom: $grid-gap
      margin-left: 1em

input::placeholder
    color: $grey

button:disabled
  cursor: not-allowed

